// @flow
import React, { memo } from 'react';
import type { Element, AbstractComponent } from 'react';
import { makeStyles, Typography, Paper } from '@material-ui/core';
import { hideIfInvalidProps } from 'utils/RenderComponent';

import { instructionContent } from 'containers/SellerOffer/NewOfferList/helpers';
import _get from 'lodash/get';

type Props = {
  offerStepId: number,
  isBestOffer: boolean,
  isSeller: Boolean,
};

const useStyles = makeStyles(theme => ({
  container: {
    minHeight: '56px',
  },
  wrapper: {
    backgroundColor: theme.palette.grey[300],
    padding: theme.spacing(2),
    borderRadius: 'unset',
    borderLeft: '5px solid',
  },
  text: {
    fontFamily: theme.typography.fontFamily.avenirLT,
    fontSize: '0.875rem',
  },
}));

const BEST_PRICE_INSTRUCTIONS = {
  seller:
    'Your NILE agent is requesting a best & final offer. Please review and accept.',
  buyer:
    'You have been prompted to enter your best & final offer. Please click the "Counter" button to proceed',
};

const checkProps = ({ offerStepId }) => offerStepId;

const Instruction = ({
  offerStepId,
  isBestOffer,
  isSeller,
}: Props): Element<'div'> => {
  const classes = useStyles();

  const getInstructionText = () => {
    if (!isBestOffer)
      return _get(instructionContent, `${offerStepId}.instruction`);

    return isSeller
      ? BEST_PRICE_INSTRUCTIONS.seller
      : BEST_PRICE_INSTRUCTIONS.buyer;
  };

  return (
    <Paper elevation={0} className={classes.wrapper}>
      <Typography className={classes.text}>{getInstructionText()}</Typography>
    </Paper>
  );
};

export default (hideIfInvalidProps(checkProps)(
  memo(Instruction),
): AbstractComponent<Props>);
